<template>
  <div class="yeardetial">
    <sHeader :name= "title"  />
    <van-collapse v-for="(item, index) in yearList" :key="index" v-model="item.active" accordion>
      <van-collapse-item  :title="item['date']" :name="1" >
          <div class="list_record"  v-for="(childItem, childIndex) in yearList[index]['child']" :key="childIndex">
            <span>{{ childItem['date'] }}</span>
            <span>高压/低压（{{childItem['heigh']}}/{{childItem['low']}}）| 脉搏（{{ childItem['pulse'] }}）</span>
          </div>
      </van-collapse-item>
    </van-collapse>
  </div>
</template>

<script>
import { onMounted, reactive, toRefs } from "vue";
import sHeader from '@/components/SimpleHeader'
import { useRoute } from "vue-router";

export default {
  name: "YearDetialList",
  components:{
    sHeader
  },
  setup(){
    const route = useRoute()

    /**
     * 声明
     * @type {UnwrapNestedRefs<{list: []}>} 数据列表
     */
    const state = reactive({
      yearList:[],
      params: {},
      title:""
    })


    onMounted(()=>{
      const { query:{ listJson, date } } = route
      state.title = date
      const list = JSON.parse(listJson)
      state.yearList = list
    })

    return {
      ...toRefs(state)
    }
  }
}
</script>

